import React, { useState, useEffect } from 'react';
import {
    Grid,
    TextField,
    Typography,
} from '@material-ui/core';
import './styles.scss';

const InputFlexComponent = (props) => {
    const {
        handleSearch,
        disabled,
        title,
        placeholder,
        onChangeSearch,
        setWordSearch,
        handleChangeText,
        isSetOutSite,
        onBlur,
        defaultValue,
        value, type,
        name
    } = props;

    const [textSearch, setTextSearch] = useState('');
    useEffect(() => {
        setTextSearch(value);
    }, [value]);



    return (
        <Grid container spacing={2} style={{padding: '8px 0'}}>
            <Grid item xs={4}>
                <Typography className='title-input-inline'>{title ?? ''}</Typography>
            </Grid>
            <Grid item xs={8}>
                <Grid style={{ display: 'flex', paddingTop:10 }} className='container-inline'>
                    <TextField
                        disabled={disabled}
                        type={type ?? ''}
                        onBlur={(val) => {
                            onBlur && onBlur(val);
                        }}
                        variant="outlined"
                        defaultValue={defaultValue}
                        placeholder={placeholder}
                        className='container'
                        onChange={handleChangeText ? (e) => handleChangeText(name, e.target.value) : null}
                        value={textSearch}
                        InputProps={{
                            className: 'search-inline',
                        }}
                    />
                </Grid>
            </Grid>
        </Grid>
    );
};

export default InputFlexComponent;
