import React, { useState } from 'react';
import LoginView from '../LoginComponent';
import { apiAcc } from '../../../api/apiConnect';
import {toastFail} from '../../../helper/Notification/utils'

const Login = (props) => {
    const { setLoading, loginSuccess } = props;
    const [user, setUser] = useState({ username: '', password: '' });
    const postLogin = () => {
        setLoading(true);
        apiAcc
            .post('/accounts/login', user)
            .then((res) => {
                sessionStorage.setItem('isLoggedIn', 'true');
                sessionStorage.setItem('token', res?.data.jwtToken);
                sessionStorage.setItem('username', user.username);
                sessionStorage.setItem('company', res?.data.accountInfo.companyId);
                sessionStorage.setItem('id', res?.data?.accountInfo?.id);
                sessionStorage.setItem('userType', res?.data.accountInfo.userType)
                sessionStorage.setItem('fullName', res?.data.accountInfo.fullName)
                res?.data.accountInfo.roles.length > 0 ? sessionStorage.setItem('roleId', res?.data.accountInfo.roles[0].id) : sessionStorage.setItem('roleId', '2')
                setLoading(false)
                loginSuccess();
            })
            .catch((err) => {
                setLoading(false)
                toastFail(' c li xy ra')
            });
    };

    return (
        <LoginView 
            postLogin={postLogin} 
            setUser={setUser} 
            user={user} 
        />
    );
};

export default Login;
