/* eslint-disable react-hooks/exhaustive-deps */
import {
  Box,
  Checkbox,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
} from '@mui/material';
import PropTypes from 'prop-types';
import Dialog from '@mui/material/Dialog';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';
import React, { useEffect, useState } from 'react';
import { toastFail } from '../../../../helper/Notification/utils';
import SearchButton from '../../../../helper/GroupButton/SearchButton';
import { convertDataOptions } from '../../../../constants/shared';
import SelectFieldCustom from '../../../../helper/SelectFieldCustom';
import InputSearchComponent from '../../../../helper/InputSearchComponent';
import TextAreaComponent from '../../../../helper/TextAreaComponent';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
      padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
      padding: theme.spacing(1),
  },
}));

const BootstrapDialogTitle = (props) => {
  const { children, onClose, ...other } = props;

  return (
      <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
          {children}
          {onClose ? (
              <IconButton
                  aria-label="close"
                  onClick={onClose}
                  sx={{
                      position: 'absolute',
                      right: 8,
                      top: 8,
                      color: (theme) => theme.palette.grey[500],
                  }}
              >
                  <CloseIcon />
              </IconButton>
          ) : null}
      </DialogTitle>
  );
};

BootstrapDialogTitle.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

export default function CreateQuestion(props) {
  const {
      showCreateQuestion,
      toggleCreateQuestion,
      categories,
      quesTypes,
      postCreateQuestion,
  } = props;

  const dataCategoryOptions = convertDataOptions(categories)
  const dataQuestionTypeOptions = convertDataOptions(quesTypes)

  const [questionSubmit, setQuestionSubmit] = useState({
      category:{
          id: ''
      },
      content: '',
      questionTime: '',
      questionType: {
          id: ''
      },
      questionChoice: [],
  });
  const [choiceA, setChoiceA] = useState({name: '', true: false})
  const [choiceB, setChoiceB] = useState({name: '', true: false})
  const [choiceC, setChoiceC] = useState({name: '', true: false})
  const [choiceD, setChoiceD] = useState({name: '', true: false})

  const handleChangeUpdateQuestion = (key, value) => {
      switch (key) {
          case 'category':
              setQuestionSubmit({...questionSubmit, category:{id: value?.value} });
              break;
          case 'questionType':
              setQuestionSubmit({...questionSubmit, questionType: {id: value?.value}});
              break;
          case 'questionTime':
              setQuestionSubmit({...questionSubmit, questionTime: value});
              break;
          case 'content':
              setQuestionSubmit({...questionSubmit, content: value});
              break;
          case 'choice_A':
              setChoiceA({...choiceA, name: value});
              break;
          case 'choice_B':
              setChoiceB({...choiceB, name: value});
              break;
          case 'choice_C':
              setChoiceC({...choiceC, name: value});
              break;
          case 'choice_D':
              setChoiceD({...choiceD, name: value});
              break;
          case 'choice_A_check':
              setChoiceA({...choiceA, true: value});
              break;
          case 'choice_B_check':
              setChoiceB({...choiceB, true: value});
              break;
          case 'choice_C_check':
              setChoiceC({...choiceC, true: value});
              break;
          case 'choice_D_check':
              setChoiceD({...choiceD, true: value});
              break;
          default:
              break;
      }
  }

  const handleClickCreateQuestion = () => {
        postCreateQuestion(questionSubmit)
  }

  useEffect(() => {
      setQuestionSubmit({...questionSubmit, questionChoice:[choiceA, choiceB, choiceC, choiceD]})
  },[choiceA, choiceB, choiceC, choiceD])

  return (
      <BootstrapDialog
          onClose={toggleCreateQuestion}
          aria-labelledby="customized-dialog-title"
          open={showCreateQuestion}
          maxWidth="lg"
          fullWidth={true}
      >
          <BootstrapDialogTitle
              id="customized-dialog-title"
              sx={{ fontFamily: 'Quicksand' }}
              onClose={toggleCreateQuestion}
          >
              To cu hi mi
          </BootstrapDialogTitle>
          <DialogContent dividers>
              <Grid container spacing={3}>
                  <Grid item xs={4}>
                      <SelectFieldCustom
                          title="Ch "
                          data={dataCategoryOptions}
                          handleOnChange={handleChangeUpdateQuestion}
                          defaultValue={questionSubmit?.category.id}
                          name="category"
                      />
                  </Grid>
                  <Grid item xs={4}>
                      <SelectFieldCustom
                          title="Loi cu hi"
                          data={dataQuestionTypeOptions}
                          handleOnChange={handleChangeUpdateQuestion}
                          defaultValue={questionSubmit?.questionType.id}
                          name="questionType"
                      />
                  </Grid>
                  <Grid item xs={4}>
                      <InputSearchComponent
                          title="Thi gian"
                          type='number'
                          name='questionTime'
                          handleChangeText={handleChangeUpdateQuestion}
                          value={questionSubmit?.questionTime ?? ''}
                      />
                  </Grid>
              </Grid>
              <Box style={{width:'100%'}}>
                  <TextAreaComponent 
                      title="Ni dung cu hi" 
                      minRows={4}
                      name='content' 
                      handleChangeText={handleChangeUpdateQuestion}
                      value={questionSubmit?.content ?? ''}
                  />
              </Box>
              <Typography sx={{paddingTop:'15px'}}>p n</Typography>
              <Grid container spacing={3}>
                  <Grid item xs={3}>
                      <Grid container>
                          <Grid item xs={2}>
                              <Box sx={{paddingTop: '10px'}}>
                                  <Checkbox 
                                      color="success"
                                      checked={choiceA.true}
                                      onChange={(e)=> handleChangeUpdateQuestion('choice_A_check', e.target.checked)}
                                  />
                              </Box>
                          </Grid>
                          <Grid item xs={10}>
                              <TextAreaComponent 
                                  minRows={4}
                                  placeholder='Nhp p n'
                                  handleChangeText={handleChangeUpdateQuestion}
                                  value={choiceA.name ?? ''}
                                  name='choice_A' 
                              />
                          </Grid>
                      </Grid>
                  </Grid>
                  <Grid item xs={3}>
                      <Grid container>
                          <Grid item xs={2}>
                              <Box sx={{paddingTop: '10px'}}>
                                  <Checkbox 
                                      color="success"
                                      checked={choiceB.true}
                                      onChange={(e)=> handleChangeUpdateQuestion('choice_B_check', e.target.checked)}
                                  />
                              </Box>
                          </Grid>
                          <Grid item xs={10}>
                              <TextAreaComponent 
                                  minRows={4}
                                  placeholder='Nhp p n'
                                  handleChangeText={handleChangeUpdateQuestion}
                                  value={choiceB.name ?? ''}
                                  name='choice_B' 
                              />
                          </Grid>
                      </Grid>
                  </Grid>
                  <Grid item xs={3}>
                      <Grid container>
                          <Grid item xs={2}>
                              <Box sx={{paddingTop: '10px'}}>
                                  <Checkbox 
                                      color="success"
                                      checked={choiceC.true}
                                      onChange={(e)=> handleChangeUpdateQuestion('choice_C_check', e.target.checked)}
                                  />
                              </Box>
                          </Grid>
                          <Grid item xs={10}>
                              <TextAreaComponent 
                                   minRows={4}
                                   placeholder='Nhp p n'
                                   handleChangeText={handleChangeUpdateQuestion}
                                   value={choiceC.name ?? ''}
                                   name='choice_C'
                              />
                          </Grid>
                      </Grid>
                  </Grid>
                  <Grid item xs={3}>
                      <Grid container>
                          <Grid item xs={2}>
                              <Box sx={{paddingTop: '10px'}}>
                                  <Checkbox 
                                      color="success"
                                      checked={choiceD.true}
                                      onChange={(e)=> handleChangeUpdateQuestion('choice_D_check', e.target.checked)}
                                  />
                              </Box>
                          </Grid>
                          <Grid item xs={10}>
                              <TextAreaComponent 
                                   minRows={4}
                                   placeholder='Nhp p n'
                                   handleChangeText={handleChangeUpdateQuestion}
                                   value={choiceD.name ?? ''}
                                   name='choice_D'
                              />
                          </Grid>
                      </Grid>
                  </Grid>
              </Grid>
            <Box className='text-right p-2'>
                <SearchButton onClick={handleClickCreateQuestion} className="button-user" title="To mi" />
            </Box>
          </DialogContent>
      </BootstrapDialog>
  );
}
