import React from 'react';
import { useHistory } from 'react-router-dom'
import { convertDateTime12hours } from '../../../constants/shared'

export default function QuizTable(props) {
    const {
        listQuiz,
        changeQuizStatus
    } = props;

    let adminLinkTo = '/'
    const history = useHistory()
    const role = sessionStorage.getItem('roleId')

    const pushHistory = (value, quiz) => {
        quiz.status === 'not start' && Number(sessionStorage.getItem('id')) === 2
        ?
            changeQuizStatus()
        :
            history.push(value.target.value)
    }

    const StatusQuiz = (props) => {
        const { active } = props;
        return (
            <div
                className={
                    active === 'done'
                        ? 'status-staff-active'
                        : 'status-staff-warning'
                }
                style={{ margin: 'auto' }}
            >
                <div style={{ padding: '2px' }}>
                    {active === 'done' ? ' lm' : 'Cha lm'}
                </div>
            </div>
        );
    }

    const RenderQuizzes = () => {

        return listQuiz.map((quiz, index) => {
            Number(role) !== 1 ? adminLinkTo = `/list-test/take-quiz/start/${quiz.id}&${index}` : adminLinkTo = `/list-test/take-quiz/finish/${quiz.id}&${index}`
            return (
                <tr key={index} style={{ height: '60px', position: 'relative' }}>
                    <td className='text-center'>{index + 1}</td>
                    <td className='text-center'>{quiz.description}</td>
                    <td className='text-center'>{quiz.numberQuestions}</td>
                    <td className='text-center'>{quiz.quizTime}</td>
                    <td className='text-center'>{convertDateTime12hours(quiz.startTime)}</td>
                    <td className='text-center'>{convertDateTime12hours(quiz.expiredTime)}</td>
                    <td>
                        <StatusQuiz active={quiz.status} />
                    </td>
                    <td>
                        <button
                            onClick={(value) => pushHistory(value, quiz)}
                            value={adminLinkTo}
                            style={{
                                height: '60px',
                                width: '100%', 
                                position: 'absolute', 
                                zIndex: 9, 
                                cursor: 'pointer', 
                                top: 0, 
                                left: 0,
                                border: 'none',
                                background: 'none',
                            }}
                        />
                    </td>
                </tr>
            )
        })
    }

    return (
        <table className="mt-3 card__table">
            <thead>
                <tr>
                    <th className='text-center'>STT</th>
                    <th className='text-center'>Tn bi kim tra</th>
                    <th className='text-center'>S cu hi</th>
                    <th className='text-center'>Thi gian lm bi</th>
                    <th className='text-center'>Thi gian m</th>
                    <th className='text-center'>Thi gian ht hn</th>
                    <th className='text-center'>Trng thi</th>
                </tr>
            </thead>
            <tbody className="">
                <RenderQuizzes />
            </tbody>
        </table>
    )
}

