export const checkStatusStaff = (status) => {
    switch (status) {
        case 'official':
            return (
                <div className='status-staff-official'>
                    <div style={{ padding: '2px' }}>Chnh thc</div>
                </div>
            );
        case 'probation':
            return (
                <div className='status-staff-probation'>
                    <div style={{ padding: '2px' }}>Th vic</div>
                </div>
            );
        case 'apprentice':
            return (
                <div className='status-staff-apprentice'>
                    <div style={{ padding: '2px' }}>Hc vic</div>
                </div>
            );
        case 'candidate':
            return (
                <div className='status-staff-candidate'>
                    <div style={{ padding: '2px' }}>ng vin</div>
                </div>
            );
        case 'escaped':
            return (
                <div className='status-staff-escaped'>
                    <div style={{ padding: '2px' }}>Ngh vic</div>
                </div>
            );
        default:
            return null;
    }
}

