export const styles = {
    previous: {
      textAlign: 'left'
    },
    secondary: {
      margin: 'auto',
      width: '80%'
    },
    reSize: {
      marginRight: '-4%',
      marginLeft: '-4%'
    },
    card: {
      borderRadius: '15px',
      width: '80%'
    },
    secondLineCard: {
      borderRadius: '15px'
    },
    cardTitle: {
      textAlign: 'left',
      color: '#5A5A5A',
      paddingLeft: '32px'
    },
    labels: {
      textAlign: 'right',
      fontWeight: 'bold',
      display: 'flex',
      flexDirection: 'column',
      color: '#5A5A5A'
    },
    content: {
      color: '#5A5A5A',
      textAlign: 'left'
    },
    col_1: {
      width: '5%',
      marginLeft: '15px'
    },
    progressBar: {
      width: '100%',
      height: '20px',
      borderRadius: '10px',
      backgroundColor: '#dfdfdf',
      position: 'relative',
      overflow: 'hidden',
    },
    doneProgress(doneTime) {
      return (
        {
          position: 'absolute',
          top: 0,
          left: 0,
          backgroundColor: '#102930',
          zIndex: 9,
          width: `${doneTime}%`,
          height: '100%',
          borderRadius: '10px',
        }
      )
    },
    endStartTime: {
      display: 'flex',
      flexDirection: 'column',
    },
    progressOuter(degrees) {
      return ({
        backgroundImage: `conic-gradient(#21C17B 0deg, #21C17B ${degrees}deg, rgba(220, 220, 220, 1) ${degrees}deg, rgba(220, 220, 220, 1) 360deg)`,
        height: '200px',
        width: '200px',
        borderRadius: '50%',
        backgroundColor: '#fff',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        boxShadow: '6px 6px 10px -1px rgba(0, 0, 0, 0.15), -6px -6px 10px -1px rgba(225, 225, 225, 0.15)',
      })
    },
    progressInner: {
      height: '138px',
      width: '138px',
      backgroundColor: '#fff',
      borderRadius: '50%',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      boxShadow: 'inset 4px 4px 6px -1px rgba(0, 0, 0, 0.2), inset -4px -4px 6px -1px rgba(225, 225, 225, 0.7), 0.5px 0.5px 0px rgba(0, 0, 0, 0.15), 0.5px 12px 10px -10px rgba(0, 0, 0, 0.05)'
    },
    diagram: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      padding: '25px 0'
    },
    diagramInfo: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      color: '#5A5A5A',
      width: '100%'
      
    }
  }
  