import React, { useEffect, useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import { apiAcc } from '../../../api/apiConnect';
import { toastFail, toastSuccess } from '../../../helper/Notification/utils';
import ClearIcon from '@mui/icons-material/Clear';
import { Box, Grid, IconButton } from '@mui/material';
import InputFlexComponent from '../../../helper/InputFlexComponent';
import { getAllRole } from '../../../api/actions';
import { convertDataOptions, dataStatusStaffIcon } from '../../../constants/shared';
import SelectFieldFlex from '../../../helper/SelectFieldFlex';
import SearchButton from '../../../helper/GroupButton/SearchButton';

function DialogAddStaff(props) {
    const { show, toggleAddStaffModal, getAllStaffs } = props;
    const token = sessionStorage.getItem('token');
    const [roleList, setRoleList] = useState([]);
    const [formAddStaff, setFormAddStaff] = useState({
        fullName: '',
        birthDay: '',
        email: '',
        address: '',
        userType: 'candidate',
        username: '',
        password: '',
        role: 2,
        phone:'',
        company: sessionStorage.getItem('company'),
        active: true,
    });
    const getRoleList = () => {
        getAllRole().then(res => setRoleList(res.data)).catch(err => toastFail('Ly danh sch vai tr tht bi !'))
    };
    const dataOptionsRole = Array.isArray(roleList) && roleList.length > 0 ? convertDataOptions(roleList) :[];
    // POST ADD STAFF
    const postAddStaff = (staff) => {
        apiAcc
            .post('/accounts', staff, {
                headers: {
                    authorization: `Bearer ${token}`,
                },
            })
            .then((res) => {
                toastSuccess('Thm nhn vin thnh cng');
                getAllStaffs();
                toggleAddStaffModal();
                setFormAddStaff({
                    fullName: '',
                    birthDay: '',
                    email: '',
                    address: '',
                    userType: 'candidate',
                    username: '',
                    password: '',
                    role: 2,
                    phone:'',
                    company: {
                        id: sessionStorage.getItem('company'),
                    },
                    active: true,
                });
            })
            .catch((err) => {
                toastFail('Tht bi, vui lng kim tra li');
            });
    };

    const handleChangeStaff = (key, value) => {
        switch (key) {
            case 'full_name':
                setFormAddStaff({ ...formAddStaff, fullName: value });
                break;
            case 'birthday':
                setFormAddStaff({ ...formAddStaff, birthDay: value });
                break;
            case 'email':
                setFormAddStaff({ ...formAddStaff, email: value });
                break;
            case 'username':
                setFormAddStaff({ ...formAddStaff, username: value });
                break;
            case 'password':
                setFormAddStaff({ ...formAddStaff, password: value });
                break;
            case 'phone':
                setFormAddStaff({ ...formAddStaff, phone: value });
                break;
            case 'address':
                setFormAddStaff({ ...formAddStaff, address: value });
                break;
            case 'role':
                setFormAddStaff({ ...formAddStaff, role: value.value });
                break;
            case 'user_type':
                setFormAddStaff({ ...formAddStaff, userType: value.value });
                break;
            default:
                break;
        }
    };

    const handleAddStaff = () => {
        postAddStaff(formAddStaff);
    };


    //Effect
    React.useEffect(() => {
        getRoleList()
    },[])

    return (
        <Modal
            show={show}
            onHide={toggleAddStaffModal}
            size="xl"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header>
                <div className="title-dialog-add">To ti khon mi</div>
                <IconButton onClick={toggleAddStaffModal}>
                    <ClearIcon />
                </IconButton>
            </Modal.Header>
            <Modal.Body>
                <div className="form_add">
                    <Grid container spacing={2}>
                        <Grid item xs={5} style={{padding: 20}}>
                            <div className="my-1 mx-auto avatar-user"></div>
                            <div className="text-center">
                                <button className="button-user">
                                    Chn nh
                                </button>
                            </div>
                            <InputFlexComponent
                                title="H v tn"
                                name="full_name"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.fullName ?? ''}
                            />
                            <InputFlexComponent
                                type='date'
                                title="Ngy sinh"
                                name="birthday"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.birthDay ?? ''}
                            />
                            <InputFlexComponent
                                type='email'
                                title="Email"
                                name="email"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.email ?? ''}
                            />
                        </Grid>
                        <Grid item xs={1}></Grid>
                        <Grid item xs={5} style={{padding: 20}}>
                            <Box style={{paddingTop:28}}></Box>
                            <InputFlexComponent
                                type='text'
                                title="Tn ng nhp"
                                name="username"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.username ?? ''}
                            />
                            <InputFlexComponent
                                type='text'
                                title="Mt khu"
                                name="password"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.password ?? ''}
                            />
                            <InputFlexComponent
                                type='number'
                                title="S in thoi"
                                name="phone"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.phone ?? ''}
                            />
                            <InputFlexComponent
                                type='text'
                                title="a ch"
                                name="address"
                                handleChangeText={handleChangeStaff}
                                value={formAddStaff.address ?? ''}
                            />
                            <SelectFieldFlex
                                title="Vai tr"
                                data={dataOptionsRole}
                                handleOnChange={handleChangeStaff}
                                defaultValue={formAddStaff.role || ''}
                                name='role'
                            />
                            <SelectFieldFlex
                                title="Trng thi"
                                data={dataStatusStaffIcon}
                                handleOnChange={handleChangeStaff}
                                defaultValue={formAddStaff.userType || ''}
                                name='user_type'
                            />
                        </Grid>
                        <Grid item xs={1}></Grid>
                    </Grid>
                    <Box sx={{textAlign:'right', width:'92%'}}>
                        <SearchButton title="To mi" onClick={handleAddStaff} />
                    </Box>
                </div>
            </Modal.Body>
        </Modal>
    );
}

export default DialogAddStaff;
