import React from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { IconButton, Typography } from '@mui/material'
import RenderQuiz from '../RenderQuizComponent'
import { getQuizByQuizId, getAllAccount } from '../../../api/actions'
import Loading from '../../../helper/Loading'

export default function QuizDetail() {
    const { id } = useParams()
    const [quizDetail, setQuizDetail] = React.useState('')

    const getQuizDetail = () => {
        getQuizByQuizId(id)
        .then((res) => {
            setQuizDetail(res.data)
        })
        .catch((err) => {
            console.error(err)
        })
    }
    React.useEffect(() => {
        getQuizDetail()
    },[])

    const [staffs, setStaffs] = React.useState('')

    React.useEffect(() => {
        getAllAccount()
        .then((res) => {
            setStaffs(res.data)
            console.log(res.data)
        })
        .catch((err) => {
            console.error(err)
        })
    },[])

    const history = useHistory()
    const pushHistory = () => {
        history.goBack()
    }

    return (
        <div>
        {
            quizDetail !== '' && staffs !== '' ?
            (
            <div className="card__list-test">
                    <div onClick={pushHistory} style={styles.backBtn}>
                        <IconButton sx={{ fontSize: 30 }}>
                            <img src="/icon/Back.svg" alt="back" />
                        </IconButton>
                        <Typography>
                            Chi tit thng tin
                        </Typography>
                    </div>
                <div className="card__header" style={styles.header}>
                    <h2>{quizDetail.description}</h2>
                    <p>S cu hi: {quizDetail.numberQuestions} cu</p>
                    <p>Thi gian lm bi: {quizDetail.quizTime} pht</p>
                    <p>Ch : {quizDetail.cate}</p>
                    <p>ng vin: {staffs.filter((staff) => staff.id === quizDetail.userId)[0].fullName}</p>
                </div>
                <div>
                    <RenderQuiz id={id}/>
                </div>
            </div>
            )
            :
            <Loading />
        }
        </div>
    )
}

const styles = {
    backBtn: {
        display: 'flex', 
        justifyContent: 'flex-start', 
        alignItems: 'center', 
        cursor: 'pointer', 
        padding: '15px 0px 10px', 
        width: 'fit-content'
    },
    header: {
        marginLeft: '5%',
    }
}