/* eslint-disable react-hooks/exhaustive-deps */
import { Box, Button, Grid } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom'
import { postUpdateStaff } from '../../../api/actions';
import {
    convertDateToApi,
    convertDateToLocal,
    dataStatusStaffIcon,
} from '../../../constants/shared';
import InputFlexComponent from '../../../helper/InputFlexComponent';
import { toastFail, toastSuccess } from '../../../helper/Notification/utils';
import SelectFieldFlex from '../../../helper/SelectFieldFlex';
import TextFieldCustom from '../../../helper/TextFieldCustom';
import CardHeader from './CardHeader';

export default function UserInformation(props) {
    const {staffInfo} = props
    const [userInfor, setUserInfor] = useState({
        id: '',
        fullName: '',
        birthDay: '',
        username: '',
        email: '',
        phone:'',
        address: '',
        userType: '',
    })
    const [isEdit, setIsEdit] = useState(true)
    const history = useHistory()

    const renderDefaultValue = () => {
        if(staffInfo){
            setUserInfor({
                id: staffInfo.id,
                fullName: staffInfo.fullName,
                birthDay: staffInfo.birthDay,
                username: staffInfo.username,
                email: staffInfo.email,
                phone:staffInfo.phone,
                address: staffInfo.address,
                userType: staffInfo.userType,
            })
        }
    }
    useEffect(() => {
        if (staffInfo) {
            renderDefaultValue();
        }
    }, [staffInfo])

    const handleClickUpdateStaff = () => {
        postUpdateStaff(userInfor)
            .then(res => {
                toastSuccess('Cp nht thng tin thnh cng !');
                setIsEdit(true)
            })
            .catch(err => {
                toastFail(' c li xy ra !');
                setIsEdit(false)
            })
    }

    const handleChangeUpdateStaff = (key, value) => {
        switch (key) {
            case 'full_name':
                setUserInfor({ ...userInfor, fullName: value });
                break;
            case 'birthday':
                setUserInfor({ ...userInfor, birthDay: value });
                break;
            case 'email':
                setUserInfor({ ...userInfor, email: value });
                break;
            // case 'username':
            //     setUserInfor({ ...userInfor, username: value });
            //     break;
            // case 'password':
            //     setUserInfor({ ...userInfor, password: value });
            //     break;
            case 'phone':
                setUserInfor({ ...userInfor, phone: value });
                break;
            case 'address':
                setUserInfor({ ...userInfor, address: value });
                break;
            // case 'role':
            //     setUserInfor({ ...userInfor, role: value.value });
            //     break;
            case 'user_type':
                setUserInfor({ ...userInfor, userType: value.value });
                break;
            default:
                break;
        }
    };

    const pushHistory = (id) => {
        history.push(`/list-test/take-quiz/${id}`)
    }
    return (
        <>
            <CardHeader
                icon="/icon/User-profile.svg"
                title="Thng tin ngi dng"
            />
            <Box sx={{ paddingTop: '20px' }}>
                <Grid container spacing={2}>
                    <Grid item xs={5}>
                        <div className="my-1 mx-auto avatar-user"></div>
                        <div className="text-center">
                            <button className="button-user">
                                Chn nh
                            </button>
                        </div>
                        <InputFlexComponent
                            disabled={isEdit}
                            title="H v tn"
                            name="full_name"
                            handleChangeText={handleChangeUpdateStaff}
                            value={userInfor.fullName ?? ''}
                        />
                        <InputFlexComponent
                            type={isEdit === true ? "text" : "date"}
                            disabled={isEdit}
                            title="Ngy sinh"
                            name="birthday"
                            handleChangeText={handleChangeUpdateStaff}
                            value={isEdit === true && userInfor.birthDay  ? convertDateToLocal(userInfor.birthDay) : userInfor.birthDay ?? ''}
                        />
                    </Grid>
                    <Grid item xs={1}></Grid>
                    <Grid item xs={5}>
                        <InputFlexComponent
                            disabled={isEdit}
                            type='email'
                            title="Email"
                            name="email"
                            handleChangeText={handleChangeUpdateStaff}
                            value={userInfor.email ?? ''}
                        />
                        {/* <InputFlexComponent
                            disabled={isEdit}
                            title="Tn ng nhp"
                            defaultValue={staffInfo?.username}
                            value={userInfor.username || ''}
                            onChange={(e) =>
                                setUserInfor({
                                    ...userInfor,
                                    username: e.target.value,
                                })
                            }
                        /> */}
                        <InputFlexComponent
                            disabled={isEdit}
                            type='number'
                            title="S in thoi"
                            name="phone"
                            handleChangeText={handleChangeUpdateStaff}
                            value={userInfor.phone ?? ''}
                        />
                        <InputFlexComponent
                            disabled={isEdit}
                            type='text'
                            title="a ch"
                            name="address"
                            handleChangeText={handleChangeUpdateStaff}
                            value={userInfor.address ?? ''}
                        />
                        <SelectFieldFlex
                            isDisable={isEdit}
                            title="Trng thi"
                            data={dataStatusStaffIcon}
                            handleOnChange={handleChangeUpdateStaff}
                            defaultValue={userInfor.userType || ''}
                            name='user_type'
                        />
                        <Grid container>
                            <Grid item xs={7} style={{ textAlign: 'right' }}>
                                {isEdit && isEdit === true ? 
                                (<Button className='button-update-user' sx={{marginTop: '20px'}} onClick={()=> setIsEdit(false)}>
                                    Chnh sa
                                </Button>) : 
                                (<>
                                    <Button className='button-save-update' sx={{margin: '20px 20px 0 0'}} onClick={handleClickUpdateStaff}>
                                        lu
                                    </Button>
                                    <Button 
                                        className='button-update-user' 
                                        sx={{marginTop: '20px'}} 
                                        onClick={() => {renderDefaultValue(); setIsEdit(true)}}>
                                        Hy
                                    </Button>
                                </>)}
                            </Grid>
                            <Grid item xs={5} style={{ textAlign: 'right' }}>
                                <Button className='button-update-user' sx={{marginTop: '20px'}} value={staffInfo ? staffInfo.id : ''} onClick={(e) => pushHistory(e.target.value)}>
                                    Xem bi kim tra
                                </Button>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={1}></Grid>
                </Grid>
                
            </Box>
        </>
    );
}
