import {
    Box,
    Button,
    Dialog,
    DialogContent,
    DialogTitle,
    Grid,
    IconButton,
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import React from 'react';

export default function DialogUploadFille(props) {
    const {openUpload, handleCloseDialog, upLoadExcelFile, onFileUpload, uploadFileState} = props;

    return (
        <div>
            <Dialog
                open={openUpload}
                onClose={handleCloseDialog}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                fullWidth={true}
            >
                <DialogTitle className="dialog-header" id="alert-dialog-title">
                    <Grid container spacing={2}>
                        <Grid item xs={11}>
                            <div className='dialog-title'>Import Excel</div>
                        </Grid>
                        <Grid item xs={1}>
                            <IconButton onClick={handleCloseDialog}>
                                <CloseIcon />
                            </IconButton>
                        </Grid>
                    </Grid>
                </DialogTitle>
                <DialogContent>
                    {/* <div className="input-import">
                        <input type="file" className="input-upload" onChange={upLoadExcelFile} />
                    </div> */}
                    <div className="custom-file input-import">
                        <input type="file" className="custom-file-input" id="inputGroupFile04" onChange={upLoadExcelFile}/>
                        <label className="custom-file-label" htmlFor="inputGroupFile04">
                            {uploadFileState ? uploadFileState?.name : 'Chn file...'}
                        </label>
                    </div>
                </DialogContent>
                <div className='group-button-upload'>
                    <Button className='cancel-button' onClick={handleCloseDialog}>Hy</Button>
                    {uploadFileState ? (
                        <Button className='import-button' onClick={onFileUpload}> Import </Button>
                    ) : null}
                </div>
            </Dialog>
        </div>
    );
}
