import { Box, Grid, IconButton, Typography } from '@mui/material';
import React, { useState } from 'react';
import { Modal } from 'react-bootstrap';
import { BoxPermission, BoxRole, TableData } from './TableData'
import ClearIcon from '@mui/icons-material/Clear';
import './styles.scss'
import { styles } from './styles'

//MODAL ADD ROLE
const AddNewRole = (props) => {
    const [nameRole, setNameRole] = useState('');
    const submitAddRole = (e) => {
        e.preventDefault();
        props.postSaveRole(nameRole);
        setNameRole('');
    };
    return (
        <Modal
            centered
            show={props.showModalAddRole}
            style={{ fontFamily: 'Quicksand' }}
        >
            <Modal.Header>
                <b className="p-2">Thm vai tr mi</b>
                <IconButton className="btn" onClick={props.toggleAddRoleModal}>
                    <ClearIcon />
                </IconButton>
            </Modal.Header>
            <Modal.Body>
                <form onSubmit={submitAddRole}>
                    <div className="form-group">
                        <label>Tn vai tr</label>
                        <input
                            type="text"
                            className="form-control"
                            value={nameRole}
                            onChange={(e) => {
                                setNameRole(e.target.value);
                            }}
                            required
                        />
                    </div>
                    <div className="row">
                        <div className="col-8"></div>
                        <div className="col-3">
                            <button type="submit" className="button-user" style={{ backgroundColor: '#121843', color: '#ffffff', padding: '5px 25px' }}>
                                <span>Thm</span>
                            </button>
                        </div>
                    </div>
                </form>
            </Modal.Body>
        </Modal>
    );
};

const RoleManageView = (props) => {
    const {
        roleDetail,
        postAddPerToRole,
        deleteRolePer,
        postSaveRole,
        showModalAddRole,
        perId,
        toggleAddRoleModal,
        setPerId,
        permissions,
        roles,
        setRoleId,
        setRoleDetailId,
        roleDetailId,
    } = props;
    return (
        <div style={{ fontFamily: 'Quicksand' }}>
            <div className='card__list-test'>
                <div className='card__header'>
                    <h3>Qun l phn quyn</h3>
                </div>
            </div>
            <div className="pt-5">
                <Grid container>
                    <Grid item xs={1}></Grid>
                    <Grid item xs={2}>
                        <Typography className='title-table-function'>Chn vai tr</Typography>
                        <BoxRole 
                            roles={roles} 
                            setRoleDetailId={setRoleDetailId} 
                            roleDetailId={roleDetailId} 
                            toggleAddRoleModal={toggleAddRoleModal} 
                        />
                    </Grid>
                    <Grid item xs={1}></Grid>
                    <Grid item xs={6}>
                        <Typography className='title-table-function'>Ty chnh chc nng cho vai tr</Typography>
                        <TableData roleDetail={roleDetail} postAddPerToRole={postAddPerToRole} />
                    </Grid>
                    <Grid item xs={1}></Grid>
                </Grid>
            </div>
            <AddNewRole
                showModalAddRole={showModalAddRole}
                toggleAddRoleModal={toggleAddRoleModal}
                postSaveRole={postSaveRole}
            />
        </div>
    );
};

export default RoleManageView;
