import React from 'react';
import UserInformation from './UserInformation';
import { Link } from 'react-router-dom';
import { IconButton, Typography } from '@mui/material';
import UserRole from './UserRole';
import './styles.scss';

function StaffInfoView(props) {
    const {
        staff,
        roles,
        roleInUser,
        postAddRoleToUser,
        deleteUserRole,
        perInUser,
        postAddPerToUser,
        setPerId,
        deleteUserPer,
        showModalPass,
        toggleModalPassChange,
        postNewPass,
    } = props;
    return (
        <div className='container-fluid'>
            <div className="card__header">
                <h3 className="">Qun l nhn vin</h3>
            </div>
            <div>
                <Link to="/admin/staff-list" style={styles.backBtn}>
                    <IconButton sx={{ fontSize: 30 }}>
                        <img src="/icon/Back.svg" alt="back" />
                    </IconButton>
                    <Typography>
                        Chi tit thng tin
                    </Typography>
                </Link>
            </div>
            <UserInformation 
                staffInfo={staff}
                toggleModalPassChange={toggleModalPassChange}
            />
            <UserRole
                roles={roles}
                roleInUser={roleInUser}
                postAddRoleToUser={postAddRoleToUser}
                deleteUserRole={deleteUserRole}
                perInUser={perInUser}
                postAddPerToUser={postAddPerToUser}
            />
        </div>
    )
}

export default StaffInfoView;

const styles = {
    backBtn: {
        display: 'flex', 
        justifyContent: 'flex-start', 
        alignItems: 'center', 
        cursor: 'pointer', 
        padding: '15px 0px 10px', 
        width: 'fit-content'
    }
}