import { Modal } from 'react-bootstrap';
import React from 'react';

export default function DialogChangePass(props) {
    const {password, setPassword, validateForm, toggleModalChangePass, show, err } = props
    return (
        <Modal centered show={show} onHide={toggleModalChangePass}>
            <Modal.Header>
                <b className="change-password">i mt khu</b>
                <button onClick={toggleModalChangePass} className="btn">
                    <i className="fa fa-times fa-2x"></i>
                </button>
            </Modal.Header>
            <Modal.Body>
                <form onSubmit={validateForm}>
                    <div className="form-group">
                        <label>Mt khu c</label>
                        <input
                            type="password"
                            className="form-control"
                            value={password.oldPass}
                            onChange={(e) => {
                                setPassword({
                                    ...password,
                                    oldPass: e.target.value,
                                });
                            }}
                        />
                    </div>
                    <div className="form-group">
                        <label>Mt khu mi</label>
                        <input
                            type="password"
                            className="form-control"
                            value={password.newPass}
                            onChange={(e) => {
                                setPassword({
                                    ...password,
                                    newPass: e.target.value,
                                });
                            }}
                        />
                        {err.minLength === true ? (
                            <span className="text-danger">
                                Password must be 6 or more characters!
                            </span>
                        ) : (
                            ''
                        )}
                    </div>
                    <div className="form-group">
                        <label>Nhp li mt khu mi</label>
                        <input
                            type="password"
                            className="form-control"
                            value={password.reNewPass}
                            onChange={(e) => {
                                setPassword({
                                    ...password,
                                    reNewPass: e.target.value,
                                });
                            }}
                        />
                        {err.match === true ? (
                            <span className="text-danger">
                                New password does not match!
                            </span>
                        ) : (
                            ''
                        )}
                    </div>
                    <div className="row">
                        <div className="col-9"></div>
                        <button
                            type="submit"
                            className="col-2 button-user px-4"
                        >
                            Lu
                        </button>
                    </div>
                </form>
            </Modal.Body>
        </Modal>
    );
}
