/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { apiAcc } from '../../../api/apiConnect';
import Box from '@mui/material/Box';
import TextFieldInfor from '../../../helper/TextFieldInfor';
import { toastFail, toastSuccess } from '../../../helper/Notification/utils';
import { getAccountById } from '../../../api/actions';
import DialogChangePass from './DialogChangePass';
import { Grid } from '@mui/material';
// import from ''

export default function AccActiveInfo() {
    const token = sessionStorage.getItem('token');
    const id = sessionStorage.getItem('id');
    const [info, setInfo] = useState('');
    //Modal Notifications

    const [show, setShow] = useState(false);
    const toggleModalChangePass = () => {
        setShow(!show);
    };

    const getAccount = () => {
        getAccountById(id)
            .then((res) => {
                setInfo(res.data);
            })
            .catch((err) => {
                toastFail('Ly thng tin tht bi');
            });
    };
    useEffect(() => {
        getAccount();
    }, []);
    // POST CHANGE PASS
    const [password, setPassword] = useState({
        username: sessionStorage.getItem('username'),
        oldPass: '',
        newPass: '',
        reNewPass: '',
    });

    const [err, setErr] = useState({
        match: false,
        minLength: false,
    });
    const validateForm = (e) => {
        e.preventDefault();
        if (password.newPass.length < 6) {
            setErr({ ...err, minLength: true });
            setTimeout(() => {
                setErr({ ...err, minLength: false });
            }, 4000);
            return;
        } else if (password.newPass !== password.reNewPass) {
            setErr({ ...err, match: true });
            setTimeout(() => {
                setErr({ ...err, match: false });
            }, 4000);
            return;
        } else {
            postChangePass();
            return;
        }
    };

    const postChangePass = (e) => {
        apiAcc
            .put('/accounts/changepass', password, {
                headers: {
                    authorization: 'Bearer ' + token,
                },
            })
            .then((res) => {
                toastSuccess('i mt khu thnh cng');
                signOut();
            })
            .catch((err) => {
                toastFail('Tht bi, vui lng th li');
            });
    };

    //Sign out
    const history = useHistory();
    const signOut = () => {
        sessionStorage.removeItem('isLoggedIn');
        sessionStorage.removeItem('token');
        sessionStorage.removeItem('username');
        sessionStorage.removeItem('company');
        sessionStorage.removeItem('id');
        history.push('/');
        window.location.reload();
    };
    return (
        <div>
            <div>
                <div className="card__list-test">
                    <div className="card__header">
                        <h3>Thng tin ti khon</h3>
                    </div>
                    <div>
                        <Box
                            sx={{
                                '& .MuiTextField-root': { m: 1, width: '75ch' },
                            }} 
                            noValidate autoComplete="off">
                            <div className="p-4">
                                <Grid container spacing={2}>
                                    <Grid item xs={1}></Grid>
                                    <Grid item xs={5}>
                                        <TextFieldInfor
                                            title="H v tn"
                                            value={info?.fullName}
                                        />
                                        <TextFieldInfor
                                            title="Email"
                                            value={info?.email}
                                        />
                                        <TextFieldInfor
                                            title="a ch"
                                            value={info?.address}
                                        />
                                        <TextFieldInfor
                                            title="V tr "
                                            value={
                                                info.userType === null
                                                    ? '(Trng)'
                                                    : info.userType
                                            }
                                        />
                                        <TextFieldInfor
                                            title="Tn ng nhp"
                                            value={info?.username}
                                        />
                                        <TextFieldInfor
                                            title="Mt khu"
                                            value="**********"
                                        />
                                        <div>
                                            <div className="py-4">
                                                <button
                                                    onClick={
                                                        toggleModalChangePass
                                                    }
                                                    className="button-user"
                                                >
                                                    i mt khu
                                                </button>
                                            </div>
                                        </div>
                                    </Grid>
                                    <Grid item xs={5}>
                                        <div className="avatar-user m-auto">
                                            <b className="name-in-avatar">
                                                {info !== ''
                                                    ? info.fullName.substr(0, 2)
                                                    : ''}
                                            </b>
                                        </div>
                                        <div>
                                            <div className="py-5">
                                                <button className="button-user">
                                                    Chn nh
                                                </button>
                                            </div>
                                        </div>
                                    </Grid>
                                    <Grid item xs={1}></Grid>
                                </Grid>
                            </div>
                        </Box>
                    </div>
                </div>
            </div>
            {/* MODAL CHANGE PASS */}
            <DialogChangePass
                password={password}
                setPassword={setPassword}
                validateForm={validateForm}
                err={err}
            />
        </div>
    );
}
