import { Button, TextField } from "@mui/material";
import React from "react";
import './style.scss'

const LoginView = (props) => {
  const { postLogin, user, setUser, show, toggleModal } =props;
  return (
    <div className="card__login container-fluid">
      <div className="row">
        <div className="card__img">
          <img src="/images/logo.png" alt="" width="50%" />
          <div>
            <img src="/images/logo-login.png" alt="" width="450px" />
          <h3 className='font-weight-bold text-white'>AI for the better life</h3>
          </div>
        </div>
        <div className="form__login">
          <p>ng nhp</p>
            <div className="form__group text-left">
              <div className="form__control">
                <TextField
                  style={{width: '100%'}}
                  variant="outlined"
                  label="Tn ng nhp"
                  type="text"
                  name="username"
                  value={user.username}
                  required
                  onChange={(e) => {
                    setUser({
                      ...user,
                      username: e.target.value,
                    });
                  }}
                />
              </div>
            </div>
            <div className="form__group text-left">
              <div className="form__control">
                <TextField
                  style={{width: '100%'}}
                  variant="outlined"
                  label="Mt khu"
                  type="password"
                  name="password"
                  value={user.password}
                  required={true}
                  onChange={(e) => {
                    setUser({
                      ...user,
                      password: e.target.value,
                    });
                  }}
                />
              </div>
            </div>
            <button onClick={postLogin}className="form__btn">ng nhp</button>
        </div>
      </div>
    </div>
  );
};

export default LoginView;
