import { Box, Button } from '@mui/material';
import React from 'react';

function SearchButton(props) {
    const { title, onClick, quizListStyle, searchQuizBarStyle, createQuizStyle } = props;
    const mergeStyle = {...styles.button, ...quizListStyle, ...searchQuizBarStyle, ...createQuizStyle}
    return (
        <Box sx={styles.groupButton}>
            <Button sx={mergeStyle} variant="contained" onClick={onClick ? onClick : null}>
                {title ?? ''}
            </Button>
        </Box>
    )
}
const styles = {
    button:{
        backgroundColor: '#161E54',
        fontSize: '12px',
        textTransform: 'capitalize'
    },
    groupButton: {
        paddingTop:4
    }
}

export default SearchButton;
