import { Grid, TextareaAutosize, Typography } from '@mui/material';
import React from 'react';
import './styles.scss'

function TextAreaComponent(props) {
    const {
        handleSearch,
        title,
        placeholder,
        onChangeSearch,
        setWordSearch,
        handleChangeText,
        isSetOutSite,
        onBlur,
        defaultValue,
        value, maxRows, minRows,
        name
    } = props;

    const [textSearch, setTextSearch] = React.useState('');
    React.useEffect(() => {
        setTextSearch(value);
    }, [value]);


    return (
        <>
            <Typography className='title-input-area'>{title ?? ''}</Typography>
            <Grid style={{ display: 'flex', paddingTop:10 }} className='container-area'>
                <TextareaAutosize
                    onBlur={(val) => {
                        onBlur && onBlur(val);
                    }}
                    maxRows={maxRows}
                    minRows={minRows}
                    defaultValue={defaultValue}
                    placeholder={placeholder}
                    className='container-area'
                    onChange={handleChangeText ? (e) => handleChangeText(name, e.target.value) : null}
                    value={textSearch}
                    InputProps={{
                        className: 'search-area',
                    }}
                    variant="outlined"
                />
            </Grid>
        </>
    );
}

export default TextAreaComponent;
