import React from "react"
import { postCreateQuiz, getAllCategories, getAllAccount } from "../../../api/actions"
import { toastFail, toastSuccess } from "../../../helper/Notification/utils"
import CreateQuizPage from "../../../components/Quiz/QuizManageComponent/CreateQuizPage"
import { useHistory } from 'react-router-dom'

export default function CreateQuizController() {

    const id = sessionStorage.getItem('id')
    // GET ALL CATEGORY
    const [categories, setCategories] = React.useState([]);
    const getAllCategoriesFunc = () => {
        getAllCategories()
            .then((res) => {
                setCategories(res.data);
            })
            .catch((err) => {
                console.log(err);
            });
    }
    React.useEffect(() => {
        getAllCategoriesFunc();
    }, []);

    // GET ALL STAFFS
    const [staffs, setStaffs] = React.useState([])
    const getAllStaffs = () => {
        getAllAccount()
            .then((res) => {
                setStaffs(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    }
    React.useEffect(() => {
        getAllStaffs()
    },[])

    // POST CREATE QUIZ
    const [createQuizForm, setCreateQuizForm] = React.useState({
        creator: +id,
        description: '',
        startTime: '',
        expiredTime: '',
        quizTime: 0,
        status: 'not start',
        userId:[]
    })
    const [topicState, setTopicState] = React.useState([{
        index: 1,
        cate: '',
        quantity: 0
    }])

    React.useEffect(() => {
        setCreateQuizForm(createQuizForm)
    },[createQuizForm])
    
    const history = useHistory()
    const postCreateQuizFunc = () => {
        const convertTopic = topicState?.map(item => ({
            cate: item.cate,
            quantity: item.quantity,
        }))
        const payload = {quiz: createQuizForm, topics: convertTopic}
        if(!createQuizForm.description){
            toastFail('Tn khng c trng')
        } else if(!createQuizForm.startTime){
            toastFail('Chn thi gian m')
        } else if(!createQuizForm.expiredTime){
            toastFail('Chn thi gian ng')
        } else if(!createQuizForm.userId.length){
            toastFail('Chn t nht mt ng vin')
        } else if(!topicState[0].cate){
            toastFail('Chn mt category')
        } else if(!topicState[0].quantity){
            toastFail('Nhp s lng')
        } else {
            postCreateQuiz(payload)
                .then(res => {
                    toastSuccess('Thnh cng');
                    setTimeout(() => history.push('/quiz/create/quiz'), 500)
                }).catch(err => {
                    toastFail(" c li xy ra");
                })
        }
    }

    return (
        <CreateQuizPage
            postCreateQuizFunc={postCreateQuizFunc}
            categories={categories}
            createQuizForm={createQuizForm}
            setCreateQuizForm={setCreateQuizForm}
            topicState={topicState}
            setTopicState={setTopicState}
            staffs={staffs}
        />
    )
}

