import React from 'react'
import { useParams, Link } from 'react-router-dom'
import { IconButton, Typography } from '@mui/material'
import moment from 'moment';
import { useInterval } from 'usehooks-ts'
import { getQuizByQuizId } from '../../../api/actions'
import Loading from '../../../helper/Loading'
import { styles } from './styles'
import RenderQuiz from '../RenderQuizComponent'
import { convertDateTime12hours, convertDateToLocal } from '../../../constants/shared'

const QuizzesDetail = (props) => {
  const {
    description,
    cate,
    numberQuestions,
    quizTime,
  } = props.quizDetail
  const {
    testedTime,
    twoDigits,
    doneQuizPerc,
    stt
  } = props
  const createDate = convertDateTime12hours(props.quizDetail.userStartQuiz)
  const totalTime = `${twoDigits(Math.floor(quizTime / 60))}:${twoDigits(quizTime%60)}:00`
  const quizPerc = ((Number(doneQuizPerc[0]) + Number(doneQuizPerc[1])) / numberQuestions) * 100
  const checkDoneTime = moment(props.quizDetail.endTime).diff(moment(props.quizDetail.userStartQuiz))
  const doneTime = checkDoneTime >= 0 ? (checkDoneTime / (quizTime * 60 * 1000)) * 100 : 100

  // ProgressBarAnimation
  const [upCount, setUpCount] = React.useState(0)
  const progressBarAnimation = () => {
    setUpCount(upCount + 1)
  }

  useInterval(
    () => {
      quizPerc !== 0 ? progressBarAnimation() : setUpCount(0)
    },
    (upCount < quizPerc) ? 12 : null,
  )

  return (
    <>
      <div className='card-information-user mt-4' style={styles.card}>
        <div style={styles.cardTitle} className='pt-5 pb-3 offset-md-1'>
          <h1>{`${Number(stt)+1}. ${description}`}</h1>
        </div>
        <div className='row pb-3'>
          <div className='col-2' style={styles.labels}>
            <p>Category</p>
            <p>Ngy to</p>
            <p>S cu hi</p>
            <p>Thi gian lm</p>
          </div>
          <div className='col-9' style={styles.content}>
            <p>{cate}</p>
            <p>{createDate}</p>
            <p>{`${numberQuestions} cu`}</p>
            <p>{`${quizTime} pht`}</p>
          </div>
        </div>
      </div>
      <div className='pt-5' style={styles.secondary}>
        <div className='row' style={styles.reSize}>
          <div className='col-6'>
            <div className='card-information-user'>
              <div className='row'>

                <div className='col-1 mt-3' style={styles.col_1}>
                  <img src='/icon/clock.png' alt='Clock icon' style={{ marginTop: '8px' }} />
                </div>

                <div className='col-10 mt-3' style={{ textAlign: 'left', padding: 0, fontSize: '1.5rem', fontWeight: 'bold', paddingRight: '20px' }}>
                  <p>Total time</p>
                  <p>{`${testedTime} / `}<span style={{ color: '#808d9d' }}>{totalTime}</span></p>
                  <div>
                    <div style={styles.progressBar}>
                      <div style={styles.doneProgress(doneTime)} />
                    </div>
                  </div>
                  <div className='mt-3 row'>
                    <div className='col-6'>
                      <div style={styles.endStartTime}>
                        <div className='row'>
                          <div className='col-7' style={{ fontSize: '1.2rem', color: '#8292A1', paddingRight: 0 }}>
                            Start Time
                          </div>
                          <div className='col-5' style={{ fontSize: '1.2rem' }}>
                            {moment(props.quizDetail.userStartQuiz).format('HH:mm')}
                          </div>
                        </div>
                      </div>
                      <div style={styles.endStartTime} className='mt-3 mb-3'>
                        <div className='row'>
                          <div className='col-7' style={{ fontSize: '1.2rem', color: '#8292A1', paddingRight: 0 }}>
                            End Time
                          </div>
                          <div className='col-5' style={{ fontSize: '1.2rem' }}>
                            {moment(props.quizDetail.endTime).format('HH:mm')}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className='col-6'>
                      <div style={styles.endStartTime}>
                        <div className='row'>
                          <div className='col-4' style={{ fontSize: '1.2rem', color: '#8292A1', paddingRight: 0 }}>
                            Date
                          </div>
                          <div className='col-8' style={{ fontSize: '1.2rem' }}>
                            {convertDateToLocal(props.quizDetail.endTime)}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='col-6'>
            <div className='card-information-user row'>
              <div className='col-6' style={styles.diagram}>
                <div style={styles.progressOuter((upCount) / 100 * 360)}>
                  <div style={styles.progressOuter((upCount) / 100 * 360)}>
                    <div style={styles.progressInner}>
                      <span style={{ fontSize: '25px', fontWeight: 'bold', color: '#084754' }}>{`${upCount}%`}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className='col-6 flex-column' style={styles.diagram}>
                <div className='p-2' style={styles.diagramInfo}>
                  <div className='col-1 align-items-center row' style={{padding: 0}}>
                    <img src='/icon/true.png' alt='true' style={{ width: '100%', height: '100%' }}/>
                  </div>
                  <div className='col-8' style={{padding: '10px 0'}}>
                    <p style={{ margin: 0 }}>Tr li ng:</p>
                  </div>
                  <div className='col-3 justify-content-start row' style={{padding: '10px 0'}}>
                    <p style={{ margin: 0 }}>{doneQuizPerc[0]}</p>
                  </div>
                </div>
                <div className='p-2' style={styles.diagramInfo}>
                  <div className='col-1 align-items-center row' style={{padding: 0}}>
                    <img src='/icon/false.png' alt='true' style={{ width: '100%', height: '100%' }}/>
                  </div>
                  <div className='col-8' style={{padding: '10px 0'}}>
                    <p style={{ margin: 0 }}>Tr li sai:</p>
                  </div>
                  <div className='col-3 justify-content-start row' style={{padding: '10px 0'}}>
                    <p style={{ margin: 0 }}>{doneQuizPerc[1]}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

// MAIN
export default function QuizFinish(props) {
  const { id, stt } = useParams()
  const [quizDetail, setQuizDetail] = React.useState({})
  const [loading, setLoading] = React.useState(false)
  const [testedTime, setTestedTime] = React.useState('')
  const regScore = /[\/ ()]/g;
  const [doneQuizPerc, setdoneQuizPerc] = React.useState({})

  //GET QUIZ DETAIL
  const getQuizDetail = () => {
    setLoading(true)
    getQuizByQuizId(id)
      .then((res) => {
        setQuizDetail(res.data)
        setLoading(false)
        moment(res.data.endTime).diff(moment(res.data.userStartQuiz)) < 0 ?
        setTestedTime(timeDiff(0, res.data.quizTime*60*1000))
        :
        setTestedTime(timeDiff(res.data.userStartQuiz, res.data.endTime))
        setdoneQuizPerc(res.data.score.split(regScore, 2))
        console.log(res.data.score.split(regScore, 2))
      })
      .catch((err) => {
        console.log(err)
        setLoading(false)
      })
  }
  React.useEffect(() => {
    getQuizDetail()
  }, [])

  const twoDigits = (num) => String(num).padStart(2, '0')
  const timeDiff = (start, end) => {
    const hours = twoDigits(Math.floor((((moment(end).diff(moment(start))) / 1000) / 60) / 60))
    const minutes = twoDigits(Math.floor(((moment(end).diff(moment(start))) / 1000) / 60) - (hours * 60))
    const seconds = twoDigits(Math.ceil((moment(end).diff(moment(start))) / 1000) % 60)
    return (`${hours}:${minutes}:${seconds}`)
  }

  return (
    <div className="py-3">
      <div style={styles.previous}>
        <Link to={`/list-test/take-quiz/${quizDetail.userId}`}>
          <IconButton sx={{ fontSize: 30 }}>
            <img src="/icon/Back.svg" alt="back" />
            <Typography sx={styles.titlePrevious}>
              Danh sch bi kim tra
            </Typography>
          </IconButton>
        </Link>
      </div>
      {
        loading === false ?
          (
            <>
              <QuizzesDetail
                quizDetail={quizDetail}
                testedTime={testedTime}
                twoDigits={twoDigits}
                doneQuizPerc={doneQuizPerc}
                stt={stt}
              />
              {
                Number(sessionStorage.getItem('roleId')) === 1 
                ?
                (<div className="p-4" style={{ width: '85%', margin: 'auto' }}>
                    <RenderQuiz 
                      id={id}
                      checkAnswerFromAdmin={'checkAnswerFromAdmin'}
                    />
                </div>)
                :
                  <div></div>
              }
            </>
          ) :
          (
            <Loading />
          )
      }
    </div>
  )
}