/* eslint-disable react-hooks/exhaustive-deps */
import { RadioGroup, FormControlLabel, Radio, IconButton, Typography } from '@mui/material';
import * as React from 'react';
import { useHistory, useParams, Link } from 'react-router-dom';
import { apiQuiz } from '../../../api/apiConnect';
import { toastFail, toastSuccess } from '../../../helper/Notification/utils';
import Loading from '../../../helper/Loading'
import PopupCustom from '../../../helper/PopupCustom'
import { styles } from './styles'
import { convertDateTime12hours } from '../../../constants/shared'

const TimeOut = (props) => {
    const {
        status,
        setStatus,
        timeCount,
        setShowQuiz,
        id,
        postSubmitAnswer,
        history
    } = props
    //Countdown Time
    const useInterval = (callback, delay) => {
        const savedCallback = React.useRef();
        // Remember the latest callback.
        React.useEffect(() => {
            savedCallback.current = callback
        }, [callback])
        // Set up the interval.
        React.useEffect(() => {
            const tick = () => {
                savedCallback.current()
            }
            if (delay !== null) {
                let id = setInterval(tick, delay)
                return () => clearInterval(id)
            }
        }, [delay])
    };
    const twoDigits = (num) => String(num).padStart(2, '0')
    const [secondsRemaining, setSecondsRemaining] = React.useState(timeCount)
    const secondsToDisplay = secondsRemaining % 60;
    const minutesRemaining = (secondsRemaining - secondsToDisplay) / 60;
    const minutesToDisplay = minutesRemaining % 60;
    const hoursToDisplay = (minutesRemaining - minutesToDisplay) / 60;

    useInterval(
        () => {
            if (secondsRemaining > 0) {
                setSecondsRemaining(secondsRemaining - 1)
            } else {
                setStatus(false)
                setShowQuiz(false)
                postSubmitAnswer()
                setTimeout(() => {
                    history.replace(`/list-test/take-quiz/finish/${id}`);
                }, 1000)
            }
        },
        status === true ? 1000 : null,
        // passing null stops the interval
    )
    return (
        <div>
            <b>
                {twoDigits(hoursToDisplay)}:{twoDigits(minutesToDisplay)}:
                {twoDigits(secondsToDisplay)}
            </b>
        </div>
    );
}

// MAIN FUNCTION
export default function StartQuiz(props) {
    const token = sessionStorage.getItem('token');
    const [quizDetail, setQuizDetail] = React.useState({});
    const [timeCount, setTimeCount] = React.useState(0);
    const [answer, setAnswer] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const history = useHistory()

    //HANDLE START QUIZ
    const [status, setStatus] = React.useState(false)
    const handleStart = () => {
        getStartQuiz()
    };

    //GET QUIZ DETAIL
    const getQuizDetail = () => {
        setLoading(true)
        apiQuiz
            .get(`/quiz/${id}`, { headers: { Authorization: token } })
            .then((res) => {
                setQuizDetail(res.data)
                const d = new Date()
                let time = d.getTime()
                res.data.userStartQuiz === 0 ?
                    setTimeCount(res.data.quizTime * 60) :
                    setTimeCount(Math.ceil(((res.data.quizTime * 60)) - ((time - res.data.userStartQuiz) / 1000)))
                    if(res.data.status !== 'done' && res.data.userStartQuiz !== 0) {
                        setStatus(true)
                        getStartQuiz()
                    }
                setLoading(false)
            })
            .catch((err) => {
                console.log(err)
                setLoading(false)
            })
    }
    React.useEffect(() => {
        getQuizDetail()
    }, [])

    //START QUIZ
    const { id, stt } = useParams();
    const [quiz, setQuiz] = React.useState([]);
    const [showQuiz, setShowQuiz] = React.useState(false);
    const getStartQuiz = () => {
        setLoading(true)
        apiQuiz
            .get(`/quiz/startquiz/${id}`, { headers: { Authorization: token } })
            .then((res) => {
                setQuiz(res.data)
            })
            .then(() => {
                setShowQuiz(true)
                setStatus(true)
                setLoading(false)
            })
            .catch((err) => {
                console.log(err)
                setLoading(false)
            })
    }

    const [quesId, setQuesId] = React.useState([]);
    const onChoiceSelect = (e, ques) => {
        if (quesId.includes(ques.questions_id) === true) {
            const newAnswer = answer.map((q) =>
                q.questions_id === ques.questions_id
                    ? {
                        quiz_id: ques?.quiz_id,
                        questions_id: ques?.questions_id,
                        questionType: {
                            id: ques?.questionType.id,
                        },
                        questionChoiceDTOs: [
                            {
                                id: e,
                                text: '',
                                userAnswer: true
                            },
                        ],
                    }
                    : q,
            );
            setAnswer(newAnswer);
        } else {
            setQuesId([...quesId, ques.questions_id]);
            setAnswer([
                ...answer,
                {
                    quiz_id: ques?.quiz_id,
                    questions_id: ques?.questions_id,
                    questionType: {
                        id: ques?.questionType.id,
                    },
                    questionChoiceDTOs: [
                        {
                            id: e,
                            text: '',
                            userAnswer: true
                        },
                    ],
                },
            ]);
        }
    };

    const choiceCheck = answer.map((a) => a.questionChoiceDTOs[0].id)

    const postSubmitAnswer = () => {
        apiQuiz
            .post('/quiz/calculate', answer, {
                headers: { Authorization: token },
            })
            .then((res) => {
                changeModalStatus()
                setQuizDetail({})
                setStatus(false)
            })
            .catch((err) => {
                console.log(err);
                toastFail('Li! Vui lng kim tra li');
            });
    };
    const RenderChoiceOfQuestion = (props) => {
        const { ques } = props;
        return ques?.questionChoiceDTOs.map((choice, idx) => (
            <div key={idx}>
                <FormControlLabel
                    style={{ color: '#565656' }}
                    value={choice.id}
                    control={
                        <Radio
                            checked={choiceCheck.includes(choice.id.toString())}
                            onChange={(e) => {
                                onChoiceSelect(e.target.value, ques);
                            }}
                        />
                    }
                    label={choice.name}
                />
            </div>
        ));
    };

    const RenderQuestion = () => {
        if (quiz.length > 0) {
            return quiz.map((ques, idx) => (
                <div
                    key={idx}
                    className="my-3 p-2 text-left"
                    style={{ borderBottom: '1px dashed #DCDCDC' }}
                >
                    <div className="row">
                        <div className="col-10">
                            <p style={{ fontSize: '1.2rem', color: '#565656' }}>
                                <span
                                    style={{ fontSize: 25, fontWeight: 'bold', marginRight: '1.2rem' }}
                                >
                                    {idx + 1}
                                </span>
                                {ques.content}
                            </p>
                        </div>
                    </div>
                    <div style={{ width: '95%', margin: 'auto' }}>
                        <RadioGroup
                            aria-labelledby="demo-radio-buttons-group-label"
                            name="radio-buttons-group"
                            column
                        >
                            <RenderChoiceOfQuestion ques={ques} />
                        </RadioGroup>
                    </div>
                </div>
            ));
        } else return null;
    }

    const StatusQuiz = (props) => {
        const { active } = props
        const mt = 'mt-1'
        return (
            <div
                className={
                    active === 'done'
                        ? `status-staff-active ${mt}`
                        : `status-staff-warning ${mt}`
                }
                style={{ marginLeft: 0 }}
            >
                <div style={{ padding: '2px', fontSize: '14px' }}>
                    {active === 'done' ? ' lm' : 'Cha lm'}
                </div>
            </div>
        )
    }

    // MODAL STATUS CONTROLLER
    const [modalStatus, setModalStatus] = React.useState(false)

    const changeModalStatus = () => {
        setModalStatus(!modalStatus)
    }

    // RECHECK RESULTS
    const [checkResult, setCheckResult] = React.useState(false)
    const [questionRemain, setQuestionRemain] = React.useState(-1)
    const [btnTypeState, setButtonTypeState] = React.useState('')

    const overrideBtnType = () => {
        setButtonTypeState(answer.length === 0 ? 'warning' : (questionRemain === 0 ? 'success' : 'warning'))
    }
    React.useEffect(() => {
        overrideBtnType()
    },[questionRemain])

    const changeCheckResult = () => {
        setQuestionRemain(quizDetail.numberQuestions - answer.length)
        setCheckResult(!checkResult)
    }

    if (loading === false) {
        return (
            <div className="py-3">
                <Typography sx={{ textAlign: 'left', color: '#0B176D', fontFamily: 'Quicksand', marginLeft: '2rem' }}>
                    <h3>Lm bi</h3>
                </Typography>
                <div style={styles.previous}>
                    <IconButton sx={{ fontSize: 30, marginLeft: '1rem' }} onClick={() => history.goBack()}>
                        <img src="/icon/Back.svg" alt="back" />
                        <Typography sx={styles.titlePrevious}>
                            Bi s {Number(stt)+1}
                        </Typography>
                    </IconButton>
                </div>
                {
                    status === false ?
                        (
                            <div className='card-information-user mt-4' style={styles.card}>
                                <div style={styles.cardTitle} className='pt-5'>
                                    <h3><b>{`${Number(stt)+1}. ${quizDetail.description}`}</b></h3>
                                </div>
                                <div className='p-3 row'>
                                    <div className='col-6' style={styles.labels}>
                                        <p className='mt-1 font-weight-bold'>Ch </p>
                                        <p className='mt-1 font-weight-bold'>S cu hi</p>
                                        <p className='mt-1 font-weight-bold'>Thi gian lm</p>
                                        <p className='mt-1 font-weight-bold'>Thi gian m</p>
                                        <p className='mt-1 font-weight-bold'>Thi gian ng</p>
                                        <p className='mt-1 font-weight-bold'>Trng Thi</p>
                                    </div>
                                    <div className='col-6' style={styles.info}>
                                        <p className='mt-1'>{quizDetail.cate}</p>
                                        <p className='mt-1'>{`${quizDetail.numberQuestions} cu`}</p>
                                        <p className='mt-1'>{`${quizDetail.quizTime} pht`}</p>
                                        <p className='mt-1'>{convertDateTime12hours(quizDetail.startTime)}</p>
                                        <p className='mt-1'>{convertDateTime12hours(quizDetail.expiredTime)}</p>
                                        <StatusQuiz active={quizDetail.status} />
                                    </div>
                                </div>
                                <div style={styles.btnGroup}>
                                    {
                                        quizDetail.status === 'not start' ?
                                            (
                                                <button
                                                    className="btn btn-outline-primary btn-lg"
                                                    onClick={handleStart}
                                                    style={styles.notStartBtn}
                                                    type="button"
                                                >
                                                    Lm bi
                                                </button>
                                            ) :
                                            (
                                                <>
                                                    <Link to={`/list-test/take-quiz/finish/${id}&${Number(stt)}`}>
                                                        <button
                                                            className="btn btn-outline-success btn-lg"
                                                            style={styles.notStartBtn}
                                                            type="button"
                                                        >
                                                            Xem kt qu
                                                        </button>
                                                    </Link>
                                                    {/* <button
                                                        className="btn btn-outline-secondary btn-lg"
                                                        onClick={handleStart}
                                                        style={styles.notStartBtn}
                                                        type="button"
                                                    >
                                                        Lm li bi
                                                    </button> */}
                                                </>
                                            )
                                    }
                                </div>
                            </div>
                        )
                        :
                        (
                            <div style={{ fontFamily: 'Quicksand' }}>
                                <div
                                    style={{
                                        textAlign: 'left',
                                        width: '90%',
                                        margin: 'auto',
                                        borderBottom: '1px dashed #DCDCDC',
                                        padding: 10,
                                    }}
                                >
                                    <h3>{quizDetail.description}</h3>
                                    <div className="row">
                                        <div className="col-6">
                                            <p>S cu hi: {quizDetail.numberQuestions}</p>
                                            <p>Thi gian: {quizDetail.quizTime} pht</p>
                                        </div>
                                        <div className="col-2 offset-md-4">
                                            <div
                                                className="button-user text-center"
                                                style={{
                                                    width: 200,
                                                    fontWeight: 'normal',
                                                    fontSize: 18,
                                                    borderRadius: 50,
                                                }}
                                            >
                                                Thi gian cn li: <br />
                                                {timeCount !== 0 ?
                                                    <TimeOut
                                                        status={status}
                                                        setStatus={setStatus}
                                                        timeCount={timeCount}
                                                        setShowQuiz={setShowQuiz}
                                                        postSubmitAnswer={postSubmitAnswer}
                                                        id={id}
                                                        history={history}
                                                    /> :
                                                    <b>NaN</b>}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="p-4" style={{ width: '90%', margin: 'auto' }}>
                                    {showQuiz === true ? (
                                        <>
                                            <RenderQuestion />
                                            <button
                                                onClick={
                                                    changeCheckResult
                                                }
                                                className="button-user"
                                                style={{ width: '100px', margin: '20px auto' }}
                                            >
                                                Submit
                                            </button>
                                        </>
                                    ) : (
                                        <div style={{ height: '70vh' }}></div>
                                    )}
                                </div>
                            </div>
                        )
                }

                <PopupCustom
                    type='success'
                    modalStatus={modalStatus}
                    changeModalStatus={changeModalStatus}
                    paragraph={[
                        'Chc mng!',
                        'Bn  hon thnh bi kim tra'
                    ]}
                    buttons={[
                        'Xem kt qu'
                    ]}
                    buttonsLink={`/list-test/take-quiz/finish/${id}&${Number(stt)}`}
                    backButton={true}
                />
                <PopupCustom
                    type={btnTypeState}
                    paragraph={
                        answer.length === 0
                        ?
                            ['hy la chn t nht 1 cu hi trc khi np bi']
                        :
                            questionRemain === 0 
                            ?
                                ['Chc mng, bn  c p n cho ton b cu hi']
                            :
                                [
                                'Ch !',
                                `Bn cn ${questionRemain} cu hi cha hon thnh`
                                ]
                    }
                    buttons={
                        answer.length === 0
                        ?
                            ['Quay li lm bi']
                        :
                        questionRemain === 0
                        ?
                            ['Quay li lm bi', 'Np bi']
                        :
                            [
                            'Lm bi tip',
                            'Np bi'
                            ]
                    }
                    checkResult={checkResult}
                    changeCheckResult={changeCheckResult}
                    postSubmitAnswer={postSubmitAnswer}
                />
            </div>
        )
    } else return (<Loading />)
}