import React, { useState, useEffect } from 'react';
import {
    Grid,
    TextField,
    Typography,
} from '@material-ui/core';
import './styles.scss';

const InputSearchComponent = (props) => {
    const {
        handleSearch,
        title,
        placeholder,
        onChangeSearch,
        setWordSearch,
        handleChangeText,
        isSetOutSite,
        onBlur,
        defaultValue,
        value, type,
        name
    } = props;

    const [textSearch, setTextSearch] = useState('');
    useEffect(() => {
        setTextSearch(value);
    }, [value]);



    return (
        <>
            <Typography className='title-input'>{title ?? ''}</Typography>
            <Grid style={{ display: 'flex', paddingTop:10 }} className='container'>
                <TextField
                    type={type ?? ''}
                    onBlur={(val) => {
                        onBlur && onBlur(val);
                    }}
                    variant="outlined"
                    defaultValue={defaultValue}
                    placeholder={placeholder}
                    className='container'
                    onChange={handleChangeText ? (e) => handleChangeText(name, e.target.value) : null}
                    value={textSearch}
                    InputProps={{
                        className: 'search',
                    }}
                />
            </Grid>
        </>
    );
};

export default InputSearchComponent;
