import React from 'react';
import { Box, Grid, Icon, Checkbox, Input } from '@mui/material';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import Modal from "react-bootstrap/Modal";
import AddCategories from './Categories/AddCategories';
import SearchButton from '../../../helper/GroupButton/SearchButton';
import TextFieldCustom from '../../../helper/TextFieldCustom';
import NotiflixReport from '../../../helper/NotiflixComponent/reportComponent';
import { convertDateTime12hours } from '../../../constants/shared'

export default function CreateQuizPage(props) {
    const {
        postCreateQuizFunc,
        categories,
        createQuizForm,
        setCreateQuizForm,
        topicState,
        setTopicState,
        staffs,
    } = props;
    const staffsList = staffs
        ? staffs.map((staff) => {
              return { value: staff.id, label: staff.fullName };
          })
        : '';

    const handleChangeStaffsList = (data) => {
        const staffsList = data.map((staff) => staff.value);
        setCreateQuizForm({ ...createQuizForm, userId: staffsList });
    };

    const handleChangeCreateQuiz = (key, value) => {
        switch (key) {
            case 'description':
                setCreateQuizForm({ ...createQuizForm, description: value });
                break;
            case 'start-time':
                setCreateQuizForm({ ...createQuizForm, startTime: value});
                break;
            case 'expired-time':
                setCreateQuizForm({ ...createQuizForm, expiredTime: value });
                break;
            case 'quiz-time':
                setCreateQuizForm({ ...createQuizForm, quizTime: Number(value) });
                break;
            default:
                break;
        }
    }

    const [modalStatus, setModalStatus] = React.useState(false)
    const [open, setOpen] = React.useState(false)
    const [warningValue, setWarningValue] = React.useState('')

    const handleWarningModal = () => {
        setOpen(!open);
    }

    const handleClose = () => {
        if(!createQuizForm.description){
            handleWarningModal()
            setWarningValue({
                icon: '/icon/Vector_warning.png',
                message: 'Tn khng c trng'
            })
        } else if(!createQuizForm.startTime){
            handleWarningModal()
            setWarningValue({
                icon: '/icon/Vector_warning.png',
                message: 'Chn thi gian m'
            })
        } else if(!createQuizForm.expiredTime){
            handleWarningModal()
            setWarningValue({
                icon: '/icon/Vector_warning.png',
                message: 'Chn thi gian ng'
            })
        } else if(!createQuizForm.userId.length){
            handleWarningModal()
            setWarningValue({
                icon: '/icon/Vector_warning.png',
                message: 'Chn t nht mt ng vin'
            })
        } else if(!topicState[0].cate){
            handleWarningModal()
            setWarningValue({
                icon: '/icon/Vector_warning.png',
                message: 'Chn ch '
            })
        } else if(!topicState[0].quantity && topicState[0].quantity > 0){
            handleWarningModal()
            setWarningValue({
                icon: '/icon/Vector_warning.png',
                message: 'Chn s lng cu hi'
            })
        } else {
            setModalStatus(!modalStatus)
        }
    }
    const [inputstatus, setInputStatus] = React.useState(true)
    const handleInputEnable = () => {
        setInputStatus(!inputstatus)
    }

    return (
        <>
            <div>
                <Link to="/quiz/create/quiz">
                    <Box className='box-header row'>
                        <Icon>
                            <img
                                src="/icon/Back.svg"
                                alt="icon"
                                weight="25"
                                height="25"
                            />
                        </Icon>
                        <div>Qun l bi kim tra</div>
                    </Box>
                </Link>
                <Grid container>
                    <Grid item xs={6}>
                        <Box className='box-input'>
                            <TextFieldCustom
                                fullWidth
                                title={`Tn bi kim tra`}
                                placeholder='Nhp tn bi kim tra'
                                values={createQuizForm.description}
                                onChange={(e) => handleChangeCreateQuiz('description',e.target.value)}
                            />
                        </Box>
                        <Box className='box-input'>
                            <TextFieldCustom
                                fullWidth
                                title={`Thi gian m`}
                                type='datetime-local'
                                values={createQuizForm.startTime}
                                onChange={(e) => handleChangeCreateQuiz('start-time',e.target.value)}
                            />
                        </Box>
                        <Box className='box-input'>
                            <TextFieldCustom
                                fullWidth
                                title={`Thi gian ng`}
                                type='datetime-local'
                                values={createQuizForm.expiredTime}
                                onChange={(e) => handleChangeCreateQuiz('expired-time',e.target.value)}
                            />
                        </Box>
                        <AddCategories
                            categories={categories}
                            topicState={topicState}
                            setTopicState={setTopicState}
                        />
                    </Grid>
                    <Grid item xs={6} style={{padding:'0px 10px'}}>
                        <Grid container className='box-multi-select'>
                            <Grid item xs={2}>
                                <div className='title'>ng vin</div>
                            </Grid>
                            <Grid item xs={10}>
                                <div className='select-multi' style={styles.selector}>
                                    <Select
                                        isMulti
                                        options={staffsList}
                                        styles={customStyles}
                                        onChange={(e) => handleChangeStaffsList(e)}
                                    />
                                </div>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Box sx={{display: 'flex', justifyContent: 'flex-end', width: '80%'}}>
                    <SearchButton
                        title="To bi kim tra"
                        searchQuizBarStyle={{
                            width: '100%',
                            height: '110%',
                            fontSize: '1rem',
                            fontWeight: 400,
                            lineHeight: 1.5,
                        }}
                        onClick={() => {
                            handleClose()
                        }}
                    />
                </Box>
            </div>
            <Modal
                show={modalStatus}
                onHide={handleClose}
                style={styles.modal}
                centered
                >
                <Modal.Body>
                    <div style={styles.modalHeader}>
                        <div style={{ width: '10vh',height: '10vh', textAlign: 'center', marginTop: '15px' }}>
                            <img src="/icon/Create_quiz_icon.svg" />
                        </div>
                        <h4 style={styles.h4}>Bn c chc chn mun to bi kim tra ny khng?</h4>
                    </div>
                    <div style={styles.modalBody}>
                        <p>{`Thi gian: `}<b>{convertDateTime12hours(createQuizForm.startTime)} - {convertDateTime12hours(createQuizForm.expiredTime)}</b></p>
                        <p>{`Ch : `}
                            <b>{categories.reduce((init, item) => 
                                    {
                                        return topicState.map((ele) => ele.cate).includes(Number(item.id)) 
                                        ? 
                                         init = [...init, (item.name.charAt(0).toUpperCase() + item.name.slice(1))] 
                                        : 
                                         init
                                    },[]).toString()
                                }
                            </b>
                        </p>
                        <p>{`S cu: `}<b>{topicState.reduce((init, item) => {return init + item.quantity}, 0)} cu</b></p>
                        <p>{`S pht d nh: `}<b>{topicState.reduce((init, item) => {return init + item.quantity}, 0)} pht</b></p>
                        <div style={styles.customTime}>
                            <Checkbox checked={!inputstatus} sx={styles.checkbox} onChange={handleInputEnable}/>
                            <span>T t thi gian lm bi thi</span>
                            <Input 
                                type="number" 
                                sx={styles.input} 
                                disabled={inputstatus}
                                values={createQuizForm.quizTime}
                                onChange={(e) => handleChangeCreateQuiz('quiz-time',e.target.value)} 
                            />
                            <span>pht</span>
                        </div>
                        <div style={styles.btnGroup}>
                            <SearchButton 
                                title='Xc nhn to'
                                onClick={postCreateQuizFunc}
                            />
                            <SearchButton 
                                title='Quay li'
                                createQuizStyle={{ 
                                    marginLeft: '15px', 
                                    backgroundColor: '#FF5151'
                                }}
                                onClick={handleClose}
                            />
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
            <NotiflixReport open={open} handleWarningModal={handleWarningModal} warningValue={warningValue}/>
        </>
    )
}

const styles = {
    selector: {
        paddingTop: '5px',
        paddingRight: '6rem',
    },
    modal: {
        top: '-5vh',
        fontFamily: `Quicksand, sans-serif`,
        color: '#525252'
    },
    h4: { 
        textAlign: 'center', 
        padding: '15px 5px', 
        fontWeight: 'bold' 
    },
    modalHeader: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center'
    },
    modalBody: {
        padding: '0 15px 25px'
    },
    customTime: {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center'
    },
    checkbox: { 
        paddingLeft: 0, 
        marginLeft: '-3px' 
    },
    input:() => ({
        width: '60px',
        height: '1.5rem',
        margin: '0 8px'
    }),
    btnGroup: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-end',
    }
}

const customStyles = {
    dropdownIndicator: () => ({
        display: 'none'
    }),
    option: (provided, state) => ({
      ...provided,
      width: '100%',
    }),
    control: (provided) => ({
        ...provided,
        width: '100%',
        height: '33vh',
        alignItems: 'flex-start',
    }),
    multiValue: (provided) => ({
        ...provided,
        width: '100%',
        justifyContent: 'space-between',
        fontSize: '1.2rem',
        marginTop: '5px',
        color: '#3577CD',
        backgroundColor: '#E7F3FF'
    }),
    multiValueLabel: (provided) => ({
        ...provided,
        color: '#3577CD',
    })
  }